*,
*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 62.5%;
  line-height: 1.15;
  -ms-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

body {
  margin: 0;
}

article,
aside,
footer,
header,
nav,
section {
  display: block;
}

h1 {
  font-size: 2em;
  margin: 0rem 0rem;
}

h2 {
  margin-block-start: 0rem;
  margin-block-end: 0rem;
}

figcaption,
figure,
main {
  display: block;
}

figure {
  margin: 0;
  margin-block-end: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
}

pre {
  font-family: monospace, monospace;
  font-size: 1em;
}

a {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
}

abbr[title] {
  border-bottom: none;
  text-decoration: underline;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: inherit;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace, monospace;
  font-size: 1em;
}

dfn {
  font-style: italic;
}

mark {
  background-color: #ff0;
  color: #000;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

audio,
video {
  display: inline-block;
}

audio:not([controls]) {
  display: none;
  height: 0;
}

img {
  border-style: none;
}

svg:not(:root) {
  overflow: hidden;
}

button,
input,
optgroup,
select,
textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

button,
input {
  overflow: visible;
}

button,
select {
  text-transform: none;
}

button,
html [type=button],
[type=reset],
[type=submit] {
  -webkit-appearance: button;
}

button::-moz-focus-inner,
[type=button]::-moz-focus-inner,
[type=reset]::-moz-focus-inner,
[type=submit]::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

button:-moz-focusring,
[type=button]:-moz-focusring,
[type=reset]:-moz-focusring,
[type=submit]:-moz-focusring {
  outline: 1px dotted ButtonText;
}

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

legend {
  box-sizing: border-box;
  color: inherit;
  display: table;
  max-width: 100%;
  padding: 0;
  white-space: normal;
}

progress {
  display: inline-block;
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=checkbox],
[type=radio] {
  box-sizing: border-box;
  padding: 0;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  -webkit-appearance: textfield;
  outline-offset: -2px;
}

[type=search]::-webkit-search-cancel-button,
[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

details,
menu {
  display: block;
}

summary {
  display: list-item;
}

canvas {
  display: inline-block;
}

template {
  display: none;
}

[hidden] {
  display: none;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "Open Sans", sans-serif;
  font-size: 1.7rem;
  background-color: #fcfcfc;
  color: #aaaaaa;
  position: relative;
  overflow-x: hidden;
}

.container-grid {
  max-width: 90vw;
  margin: 0 auto;
  display: grid;
  grid-column: 100%;
  grid-template-rows: calc(100vh - 7rem);
}

::selection {
  background-color: #25d7ff;
  color: #fcfcfc;
}

.heading {
  font-family: "Bebas Neue", cursive;
  color: #272727;
}
.heading--primary {
  font-size: 7rem;
}

.paragraph {
  line-height: 1.7;
}

.u-mt-s {
  margin-top: 1.5rem !important;
}

.btn:link, .btn:visited {
  font-size: 2rem;
  font-family: "Bebas Neue", cursive;
  letter-spacing: 1px;
  text-decoration: none;
  background-color: #fcfcfc;
  border: 2px solid #00a8cc;
  padding: 1rem 4rem;
  border-radius: 10rem;
  color: #00a8cc;
  transition: all 0.4s;
}
.btn:hover {
  background-color: #00a8cc;
  color: #fcfcfc;
}

.logo {
  height: 2.5rem;
}
.logo__img {
  height: 100%;
}

.header {
  height: 7rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0rem 5vw;
  box-shadow: 0 0 2rem rgba(39, 39, 39, 0.3);
  z-index: 100000;
  background-color: #fcfcfc;
}

.navigation__menu {
  position: relative;
}
.navigation__list {
  display: flex;
  list-style: none;
  column-gap: 3rem;
}
.navigation__item {
  font-family: "Bebas Neue", cursive;
  font-size: 2rem;
}
.navigation__link:link, .navigation__link:visited {
  text-decoration: none;
  color: #272727;
  transition: all 0.3s;
}
.navigation__link:hover {
  color: #00a8cc;
}
.navigation__toggle {
  display: none;
  font-size: 3.5rem;
  position: absolute;
  right: 5vw;
  top: 1.5rem;
  color: #272727;
  padding: 5px;
  cursor: pointer;
}

.hero {
  display: flex;
  justify-content: space-between;
  align-items: center;
  overflow: hidden;
}
.hero__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  row-gap: 1rem;
}
.hero__paragraph {
  width: 50rem;
}
.hero__img {
  flex: 0 0 50%;
  position: relative;
}
.hero__imagen {
  width: 50rem;
  z-index: 10;
  transform: translate(15rem, -2rem);
}

.section-about {
  background-color: blueviolet;
}

.section-information {
  background-color: crimson;
}

.section-puppies {
  background-color: darkgreen;
}

.section-review {
  background-color: darkkhaki;
}

.section-contact {
  background-color: royalblue;
}

.bg {
  width: 90rem;
  height: 85rem;
  background-color: #00a8cc;
  clip-path: circle(50% at 50% 50%);
  position: absolute;
  right: 0;
  transform: translate(13.3rem, -13rem);
  z-index: -1;
}